

:root {
    --PrimaryColor: hsl(199, 100%, 33%);
    --SecondaryColor: hsl(187, 85%, 43%);
    --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greyText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
    --bodyColor: rgn(240, 240, 246);
    --cardBG: rgb(225, 225 235);
}

.header {
   position: fixed;
   display: flex;
   align-items: center;
   justify-content: space-between;
   background: rgb(178, 187, 196);
   width: 100%;
   padding: 1rem;
   z-index: 1000;
   box-shadow: 0 2px 4px #000;
  
   .logo {
    color: var(--whiteColor);
    font-weight: 600;
    cursor: pointer;
    align-items: center;
    justify-content: center;

     .icon {
       font-size: 25px;
       color: var(--whiteColor);
       transform: translateY(5px);
     }
   }

  
    .nav-btn {
      padding: .6rem 1.5rem;
      background: var(--whiteColor);
      border-radius: 3rem;
      border: none;
      outline: none;
      cursor: pointer;
      a {
        color: var(--PrimaryColor);
        font-weight: 600;
      }
    }

    /* Hover effects */
    .nav-btn:hover {
      background: var(--PrimaryColor);
      transition-duration: 0.5s;
    }

    .nav-btn:hover a {
      color: var(--whiteColor);
      font-weight: 600;
    }
    
   
   @media screen and (max-width: 768px) {
     .navBar {
        position: absolute;
         background: var(--whiteColorDeam);
        height: max-content;
        width: 80%;
        border-radius: 1rem;
        top: -500rem;
        left: 50%;
        padding: 1rem;
        transform: translate(-50%);
        z-index: 2000;
        box-shadow: 0 2px 4px rgba( 2, 15, 29, 0.904);
        transition: .2s ease-in-out;

        .navLists {
            flex-direction: column;
            justify-content: center;
            width: 100%;
            margin: auto;
            padding: .5rem 0;
            background: rgb(178, 187, 196);
            border-radius: 5px;
            

            .navItem {
               padding: .5rem 0;
               
               .navLink {
                 color: var(--whiteColor);
                 font-size: .9rem;
                 font-weight: 600;
                 &:hover {
                    color: var(--PrimaryColor);
                 }
               }
            }

            .btn {
              a {
                font-weight: 600;
                color: var(--whiteColor);
              }
            }
        }

        .closeNavbar {
          position: absolute;
          top: 1rem;
          right: 1.5rem;
          color: var(--whiteColor);
          &:hover {
            color: var(--SecondaryColor);
          }
        }
     }

     .activeNavbar {
       top: 7rem;
     }
   }

   .toggleNavbar {
      .icon {
        font-size: 25px;
        color: var(--whiteColor);
      }
      &:hover.icon {
        color: var(--secondaryColor);
      }
   }
}


/* Media Queries */

@media screen and (min-width: 769px) {
  .toggleNavbar, .closeNavbar {
    display: none;
  }

  .header{
    padding: 1.5rem 2rem;

    .navBar {
      .navLists {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: row;

        .navItem {
          .navLink {
            color: var(--whiteColor);
            padding: 0 0.45rem;
            font-size: 0.9rem;
            font-weight: 500;

            &:hover {
              color: var(--PrimaryColor);
            }
          }
        }

        .btn {
          font-weight: 500;
        }
      }
    }
  }
}

@media screen and (min-width: 960px) {
  .header {
    .navBar {
      .navLists {
        .navItem {
          padding: 0  0.7rem;
        }
      }
    }
  }
}


@media screen and (min-width: 1240px) {
  .header {
    padding: 1rem 8.5rem;
  }
}
