
:root {
    --PrimaryColor: hsl(199, 100%, 33%);
    --SecondaryColor: hsl(187, 85%, 43%);
    --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greyText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
    --bodyColor: rgn(240, 240, 246);
    --cardBG: rgb(225, 225 235);
    --cardHover: rgb(255, 255, 255);
    --starColor: rgb(255, 204, 0);
    --pinColor: rgb(31, 147, 13);
}


.footer{
    width: 100%;
    position: relative;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
     margin: auto;
    .videoDiv{
        position: absolute;
        height: 100%;
        width: 100%;
        video{
          height: 100%;
          object-fit: cover;
        }

        &::after{
            content: '';
            position: absolute;
            background: rgba(10, 84, 107, 0.307);
            height: 100%;
            width: 100%;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            mix-blend-mode: multiply;
        }
    }

    .secContent{
        width: 100%;
        height: max-content;
        padding: 2rem initial;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: auto;
        row-gap: 2rem;
        z-index: 100;

        .contactDiv{
            justify-content: space-between;
            flex-direction: column;
            gap: 1rem;

            .text{
                small{
                    font-size: 13px;
                    font-weight: 400;
                    color: var(--whiteColor);
                }

                h2{
                    color: var(--whiteColor);
                    font-size: 1.8rem;
                }
            }

            .inputDiv{
                width: 100%;
                gap: 1rem;
                flex-direction: column;
                input{
                    width: 100%;
                  padding: .5rem .6rem;
                  border-radius: 3rem;
                  border: 1px solid var(--whiteColor);
                  outline: none;
                  background: transparent;
                  color: var(--whiteColor);
                  &::placeholder{
                    color: var(--whiteColor);
                    opacity: .5;
                  }
                   
                }

                

                .footer-btn{
                    width: 100%; //at a wider screen, width will be max-content.
                    justify-content: center;
                    padding: .6rem 1.5rem;
                    color: var(--whiteColor);
                    background: rgb(178, 187, 196);
                    font-weight: 600;
                    gap: .2rem;
                    border-radius: 3rem;
                    border: none;
                    outline: none;
                    cursor: pointer;
                    .icon{
                        font-size: 18px;
                    }
                }
                .footer-btn:hover {
                    background: var(--PrimaryColor);
                }
                
                .footer-btn a {
                    color: var(--whiteColor);
                }
            }
        }

        .footerCard{
          position: relative;
          padding: 1rem 1rem 4rem 1rem;
          gap: 2rem;
          border-radius: 1rem;
          background: var(--bodyColor);
          width: 100%;
          justify-content: center;
          flex-direction: column;
          align-items: flex-start;
          overflow: hidden;


          .footerIntro{
            flex-basis: 50%;
            justify-content: center;
            align-items: flex-start;
            flex-direction: column;
            row-gap: .5rem;

            .logoDiv{
                position: relative;
                .logo{
                    color: var(--whiteColor);
                    font-weight: 600;
                    position: absolute;
                    bottom: -5px;
                    
                    span {
                        position: absolute;
                        bottom: -0.438rem;
                        right: -3.875rem;
                        font-size: 1.2rem;
                    }
                    
                    .icon{
                        font-size: 25px;
                        color: var(--whiteColor);
                        transform: translateY(5px);
                       }
                }
            }

            .footerParagraph{
                margin-top: 0.313rem;
                font-size: 0.813rem;
                color: var(--whiteColor); 
            }

           
          }

           .footerLinks{
              flex-basis: 50%;
              width: 100%;
              gap: 1rem;
              grid-template-columns: repeat(2,1fr);
              justify-content: space-between;
              position: relative;

              .linkGroup{
                .groupTitle{
                    display: block;
                    font-weight: 600;
                    color: var(--whiteColor);
                    margin-bottom: .5rem;
                }

                .socialTitle {
                    position: absolute;
                    bottom: 30px;
                    font-size: 1rem;
                }

                .footerSocials{
                    column-gap: .5rem;
                    .icon{
                        color: var(--whiteColor);
                        font-size: 2rem;
                        &:hover{
                            color: var(--PrimaryColor);              
                        }
                    }
                }

                .footerList{
                    font-size: 15px;
                    color: var(--whiteColor);
                    transition: .3s ease-in-out;
                    .icon{
                        font-size: 15px;
                        color: var(--greyText);
                    }

                    &:hover{
                        color: var(--PrimaryColor);
                        transform: translateX(7px);
                        .icon{
                            color: var(--SecondaryColor);
                        }
                    }


                }
              }
            }

        }
    }

    .footer-container{
        position: absolute;
        bottom: -2px;
        font-size: 1rem;
        color: var(--whiteColor);
      }
}

/* Media Queries */


@media screen and (min-width: 320px) and (max-width: 499px) {
    .footer {
        .secContent {
            
            .contactDiv {

                .inputDiv {
                    p {
                        color: var(--whiteColor);
                    } 
                }
            }

            .footerCard {

                .footerIntro {
                    position: relative;
    
                    .logoDiv {
                        display: none;
                    }
    
                    .footerParagraph {
                        display: none;
                    }
    
                    .footerSocials {
                        column-gap: .5rem;
                        
                        .icon {
                            color: var(--textColor);
                            font-size: 1.563rem;
                            &:hover {
                                color: var(--PrimaryColor);
                            }
                        }
                    }
                }
                .footerLinks {
                    grid-template-columns: repeat(2, 1fr);
                    padding-bottom: 1rem;
                    width: 80%;
                }

                .footer-container {
                   justify-content: space-between;
                   flex-direction: row;
                   font-size: 1.2rem;
                }
            }
        }
    }
}

@media screen and (min-width: 500px) {
    .footer {
        .secContent {

            .contactDiv {

                .inputDiv {
                    p {
                        color: var(--whiteColor);
                    } 
                }
            }

            .footerCard {

                .footerIntro {
                    position: relative;
    
                    .logoDiv {
                        display: none;
                    }
    
                    .footerParagraph {
                        display: none;
                    }
    
                    .footerSocials {
                        column-gap: .5rem;
                        
                        .icon {
                            color: var(--textColor);
                            font-size: 1.563rem;
                            &:hover {
                                color: var(--PrimaryColor);
                            }
                        }
                    }
                }
                .footerLinks {
                    grid-template-columns: repeat(2, 1fr);
                    padding-bottom: 1rem;
                    width: 90%;
                }

            }
        }

        .copyright {
            font-size: 0.875rem;
        }
    }
}


@media screen and (min-width: 660px) {
    .footer {
        padding: 5rem 0;
        .secContent {

            .contactDiv {

                .inputDiv {
                    p {
                        color: var(--whiteColor);
                    }
                }
            }

           .footerCard {
             flex-direction: column;


             .footerIntro {
                position: relative;

                .logoDiv {
                    display: none;
                }

                .footerParagraph {
                    display: none;
                }

                .footerSocials {
                    column-gap: .5rem;
                    
                    .icon {
                        color: var(--textColor);
                        font-size: 1.563rem;
                        &:hover {
                            color: var(--PrimaryColor);
                        }
                    }
                }
            }

             .footerLinks {
                grid-template-columns: repeat(4,1fr);
                padding-bottom: 1rem;
                width: 90%;
             }
           }
        }
    }
}


@media screen and (min-width: 840px) {
    .footer {
        .secContent {
           flex-direction: row;
           justify-content: space-between;

           .inputDiv {
             width: max-content;
             flex-direction: row;
             justify-content: flex-start;

             input {
                width: 60%;
             }

             .btn {
                width: max-content;
             }
           }
        }
        .copyright {
            font-size: 0.875rem;
        }
    }
}


@media screen and (min-width: 1024px) {
    .footer {
        .secContent {

            .contactDiv {

                .inputDiv {
                    p {
                        color: var(--whiteColor);
                    }
                    
                }
            }
            


            .footerCard {
                padding: 3rem 1rem;
                
                .footerIntro {
                    position: relative;

                    .logoDiv {
                        display: none;
                    }

                    .footerParagraph {
                        display: none;
                    }

                    .footerSocials {
                        column-gap: .5rem;
                        
                        .icon {
                            color: var(--textColor);
                            font-size: 1.563rem;
                            &:hover {
                                color: var(--PrimaryColor);
                            }
                        }
                    }
                }

                .footerLinks {
                    position: relative;
                    grid-template-columns: repeat(4, 1fr);
                    .linkGroup {

                        .socialTitle {
                            position: absolute;
                            top: -104px;
                        }

                        .footerSocials {
                            position: absolute;
                            top: -47px;

                        }
                        .footerList {
                            font-size: 1rem;
                            padding: 5px 0;
                        }
                    }
                }
            }
        }
        .copyright {
            font-size: 1rem;
        }
    }
}

