:root {
  --PrimaryColor: hsl(199, 100%, 33%);
  --SecondaryColor: hsl(187, 85%, 43%);
  --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(201, 33%, 16%);
  --textColor: hsl(240, 4%, 36%);
  --whiteColorDeam: hsl(0, 0%, 93%);
  --greyText: rgb(190, 190, 190);
  --inputColor: rgb(239, 239, 239);
  --bodyColor: rgn(240, 240, 246);
  --cardBG: rgb(225, 225 235);
  --cardHover: rgb(255, 255, 255);
  --starColor: rgb(255, 204, 0);
  --pinColor: rgb(31, 147, 13);
}

.footer {
  width: 100%;
  position: relative;
  padding: 2rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.footer .videoDiv {
  position: absolute;
  height: 100%;
  width: 100%;
}
.footer .videoDiv video {
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.footer .videoDiv::after {
  content: "";
  position: absolute;
  background: rgba(10, 84, 107, 0.307);
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  mix-blend-mode: multiply;
}
.footer .secContent {
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  padding: 2rem initial;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: auto;
  row-gap: 2rem;
  z-index: 100;
}
.footer .secContent .contactDiv {
  justify-content: space-between;
  flex-direction: column;
  gap: 1rem;
}
.footer .secContent .contactDiv .text small {
  font-size: 13px;
  font-weight: 400;
  color: var(--whiteColor);
}
.footer .secContent .contactDiv .text h2 {
  color: var(--whiteColor);
  font-size: 1.8rem;
}
.footer .secContent .contactDiv .inputDiv {
  width: 100%;
  gap: 1rem;
  flex-direction: column;
}
.footer .secContent .contactDiv .inputDiv input {
  width: 100%;
  padding: 0.5rem 0.6rem;
  border-radius: 3rem;
  border: 1px solid var(--whiteColor);
  outline: none;
  background: transparent;
  color: var(--whiteColor);
}
.footer .secContent .contactDiv .inputDiv input::-moz-placeholder {
  color: var(--whiteColor);
  opacity: 0.5;
}
.footer .secContent .contactDiv .inputDiv input::placeholder {
  color: var(--whiteColor);
  opacity: 0.5;
}
.footer .secContent .contactDiv .inputDiv .footer-btn {
  width: 100%;
  justify-content: center;
  padding: 0.6rem 1.5rem;
  color: var(--whiteColor);
  background: rgb(178, 187, 196);
  font-weight: 600;
  gap: 0.2rem;
  border-radius: 3rem;
  border: none;
  outline: none;
  cursor: pointer;
}
.footer .secContent .contactDiv .inputDiv .footer-btn .icon {
  font-size: 18px;
}
.footer .secContent .contactDiv .inputDiv .footer-btn:hover {
  background: var(--PrimaryColor);
}
.footer .secContent .contactDiv .inputDiv .footer-btn a {
  color: var(--whiteColor);
}
.footer .secContent .footerCard {
  position: relative;
  padding: 1rem 1rem 4rem 1rem;
  gap: 2rem;
  border-radius: 1rem;
  background: var(--bodyColor);
  width: 100%;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  overflow: hidden;
}
.footer .secContent .footerCard .footerIntro {
  flex-basis: 50%;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  row-gap: 0.5rem;
}
.footer .secContent .footerCard .footerIntro .logoDiv {
  position: relative;
}
.footer .secContent .footerCard .footerIntro .logoDiv .logo {
  color: var(--whiteColor);
  font-weight: 600;
  position: absolute;
  bottom: -5px;
}
.footer .secContent .footerCard .footerIntro .logoDiv .logo span {
  position: absolute;
  bottom: -0.438rem;
  right: -3.875rem;
  font-size: 1.2rem;
}
.footer .secContent .footerCard .footerIntro .logoDiv .logo .icon {
  font-size: 25px;
  color: var(--whiteColor);
  transform: translateY(5px);
}
.footer .secContent .footerCard .footerIntro .footerParagraph {
  margin-top: 0.313rem;
  font-size: 0.813rem;
  color: var(--whiteColor);
}
.footer .secContent .footerCard .footerLinks {
  flex-basis: 50%;
  width: 100%;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  justify-content: space-between;
  position: relative;
}
.footer .secContent .footerCard .footerLinks .linkGroup .groupTitle {
  display: block;
  font-weight: 600;
  color: var(--whiteColor);
  margin-bottom: 0.5rem;
}
.footer .secContent .footerCard .footerLinks .linkGroup .socialTitle {
  position: absolute;
  bottom: 30px;
  font-size: 1rem;
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerSocials {
  -moz-column-gap: 0.5rem;
       column-gap: 0.5rem;
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerSocials .icon {
  color: var(--whiteColor);
  font-size: 2rem;
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerSocials .icon:hover {
  color: var(--PrimaryColor);
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerList {
  font-size: 15px;
  color: var(--whiteColor);
  transition: 0.3s ease-in-out;
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerList .icon {
  font-size: 15px;
  color: var(--greyText);
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerList:hover {
  color: var(--PrimaryColor);
  transform: translateX(7px);
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerList:hover .icon {
  color: var(--SecondaryColor);
}
.footer .footer-container {
  position: absolute;
  bottom: -2px;
  font-size: 1rem;
  color: var(--whiteColor);
}

